*{
  margin: 0;
  padding: 0;
}

.app-wrapper{
  max-width: 1200px;
  display:grid;
  grid-template-areas:
    'h h'
    'n c';

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content{
  background-color: #888888;
  grid-area: c;
}